.timeline {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 85px 20px;
  border-bottom: 1px solid #0d6efd;
  background: linear-gradient(rgba(0, 0, 0, 0.98), rgba(0, 0, 0, 0.98)), url("./../images/geometric.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Cuprum", sans-serif;
  position: relative;
}

.timeline-header {
  padding-bottom: 40px;
  color: white;
  font-family: "Fira Mono", monospace;
}

.timeline-title {
  font-size: 3rem;
}

.timeline-subtitle {
  display: none;
}

.timeline-items-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
}

.timeline-items-wrapper::before {
  left: calc(50% - 1px);
  content: "";
  position: absolute;
  top: 325px;
  bottom: 100px;
  width: 2px;
  background-color: #0d6efd;
}

.timeline-item-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 10px;
  padding: 5px 10px;
  height: 200px;
  max-width: 400px;
  border-radius: 10%;
}

.timeline-image-container {
  height: 120px;
  margin-left: 15px;
}

.timeline-item-wrapper:nth-child(even) {
  flex-direction: row-reverse;
}
.timeline-item-wrapper:nth-child(even) .timeline-image-container {
  margin-right: 15px;
}

.timeline-image {
  height: 100%;
  width: 120px;
  border-radius: 15%;
  object-fit: cover;
  object-position: center center;
}

.tile {
  width: 55vw;
}

.tile-body {
  padding: 0 15px;
}

.timeline-item-wrapper:nth-child(1) {
  background-color: #24a5dc;
}

.timeline-item-wrapper:nth-child(2) {
  background-color: #df802e;
}

.timeline-item-wrapper:nth-child(3) {
  background-color: #25d13c;
}

.timeline-item-wrapper:nth-child(4) {
  background-color: #f04419;
}

.timeline-item-wrapper:nth-child(6) {
  background-color: #a624e7;
}

.timeline-item-wrapper:nth-child(5) {
  background-color: #d6d019;
}

.timeline-item-wrapper:nth-child(7) {
  background-color: #0d6efd;
}

.final-image-wrapper {
  margin: 40px;
  display: none;
}

.tile-grow {
  transition: all 0.4s ease-in-out;
}

.tile-grow:hover {
  transform: scale(1.2);
}

@media screen and (min-width: 700px) {
  .timeline-item-wrapper:nth-child(odd) {
    margin-right: 35%;
  }
  .timeline-item-wrapper:nth-child(odd) .timeline-image-container {
    height: 120px;
    transform: translateX(400%);
  }
  .timeline-item-wrapper:nth-child(odd) .timeline-image {
    border-radius: 50%;
    min-width: 120px;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
  .timeline-item-wrapper:nth-child(odd) .tile {
    margin-right: 5vw;
  }
  .timeline-item-wrapper:nth-child(odd) .tile-body {
    width: 300px;
    padding-right: 40px;
    align-self: center;
    transform: translateX(-10%);
  }
  .timeline-item-wrapper:nth-child(odd) .tile-title {
    font-size: 2.5rem;
  }

  .timeline-item-wrapper:nth-child(even) {
    margin-left: 25%;
  }
  .timeline-item-wrapper:nth-child(even) .timeline-image-container {
    height: 120px;
    transform: translateX(-400%);
  }
  .timeline-item-wrapper:nth-child(even) .timeline-image {
    border-radius: 50%;
    min-width: 120px;
    height: 100%;
    object-fit: cover;
    object-position: center center;
  }
  .timeline-item-wrapper:nth-child(even) .tile-body {
    width: 300px;
    align-self: center;
    padding-left: 20%;
    transform: translateX(15%);
  }
  .timeline-item-wrapper:nth-child(even) .tile-title {
    font-size: 2.5rem;
  }
}
@media screen and (min-width: 700px) and (min-width: 900px) {
  .timeline-item-wrapper {
    margin-bottom: 5vh;
  }

  .timeline-items-wrapper::before {
    bottom: 150px;
  }
}
@media screen and (min-width: 700px) and (min-width: 1050px) {
  .timeline-items-wrapper {
    max-width: 1050px;
  }

  .timeline-item-wrapper:nth-child(odd) {
    margin-left: -24%;
  }
  .timeline-item-wrapper:nth-child(odd) .timeline-image-container {
    height: 120px;
    transform: translateX(405%);
  }

  .timeline-item-wrapper:nth-child(even) {
    margin-left: 60%;
  }
  .timeline-item-wrapper:nth-child(even) .timeline-image-container {
    transform: translateX(-385%);
  }

  .timeline-items-wrapper::before {
    bottom: 200px;
  }

  .timeline-item-wrapper.tile-title {
    font-size: 3rem;
  }

  .tile-detail {
    font-size: 1.25rem;
  }

  .final-image-wrapper {
    display: block;
    z-index: 4;
  }
  .final-image-wrapper .image-container {
    border-radius: 50%;
  }
  .final-image-wrapper .timeline-image {
    border-radius: 50%;
    height: 120px;
  }
}
